/**
 * menu.api
 */

import keyCode from '../../config/keyCode.json'
import tip from '../Message/tip'

export default {
  methods: {
    /**
     *  css  class 
     */
    defaultValClassName(value) {
      return this.defaultVal === value ? `${this.cPrefix}-default-text` : ''
    },

    /**
     * 
     *
     */
    clickParent() {
      if (this.panelDisplay) {
        return this._togglePanelDisplay(false)
      }
    },

    /**
     * 
     *
     * @return {Object} this - 
     */
    blur() {
      this.focusing = false
    },

    /**
     * 
     *
     * @return {Object} this - 
     */
    focus() {
      this.focusing = true
    },

    /**
     * 
     *
     * @return {Object} this - 
     */
    click(event) {
      if (this.clicking) {
        return false
      }

      this.clicking = true

      event.stopPropagation()

      setTimeout(() => {
        this.clicking = false

        return this._togglePanelDisplay()
      }, 100)
    },

    /**
     * keydown
     */
    keydown(event) {
      if (!this.focusing) {
        return false
      }

      if (event.keyCode === keyCode.enter) {
        this._togglePanelDisplay()
      }
    },

    /**
     * 
     * @return {Object} this - 
     */
    spread() {
      return this._togglePanelDisplay(true)
    },

    /**
     * 
     * @return {Object} this - 
     */
    fold() {
      return this._togglePanelDisplay(false)
    },

    /**
     * / 
     * @return {Object} this - 
     */
    toggle() {
      return this._togglePanelDisplay()
    },

    /**
     * 
     */
    adjust(cb) {
      this.$refs.motion.adjustMotion()
    }
  }
}



