/**
 * select-option --  select  option 
 *
 * @prop multiple - 
 * @prop option - option
 * @prop optRoot - 
 * @prop valName -  options  value  key name
 * @prop txtName -  options  text  key name
 *
 * @event change - checkboxoption
 * @event changeScroller - /
 */

import './SelectOpt.scss'
import Vue from 'vue'
import render from './SelectOpt.render'
import compEvent from '../../config/event.json'

import iconComp from '../Icon/Icon'
import checkComp from '../Check/Check'
import listComp from '../List/List'
import rowComp from '../Row/Row'
import colComp from '../Col/Col'

import MotionRip from '../MotionRip/MotionRip'
import baseMixin from '../../mixin/base'

export default {
  name: 'SelectOpt',

  render,

  mixins: [baseMixin],

  components: {
    icon: iconComp,
    check: checkComp,
    list: listComp,
    row: rowComp,
    column: colComp,
    'motion-rip': MotionRip
  },

  props: {
    option: {
      type: Array,
      default: () => []
    },

    multiple: {
      type: Boolean,
      default: false
    },

    optRoot: {
      type: Object,
      default: () => {
        return {}
      }
    },

    valName: {
      type: String,
      default: 'value'
    },

    txtName: {
      type: String,
      default: 'text'
    }
  },

  data() {
    return {
      //  check  option
      selectedAllCheckOpt: [{
        value: -1,
        text: ''
      }],
      pressing: false
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-select-opt`
    }
  },

  methods: {
    _binder() {
      this.$refs.list.$on('scrollerChange', () => {
        this.$emit('changeScroller', {
          emitter: this
        })
      })
    },

    //  li  class 
    liClass(classify, value) {
      return [
        `${this.cPrefix}-li`,
        this.optRoot.defaultValClassName(value),
        { [`${this.cPrefix}-classify-title`]: classify }
      ]
    },

    /**
     * @param {Object} 
     * @return {Boolean}
     */
    hasSubOption(item) {
      return Array.isArray(item.sub) && item.sub.length > 0
    },

    /**
     * @param {Object} 
     * @return {Function}
     */
    selectOption(evt, index) {
      evt.stopPropagation()

      let option = this.option[parseInt(index - 1, 10)]

      if (option.classify) {
        return false
      }

      this.$emit('change', {
        emitter: this,
        value: option[this.valName],
        text: option[this.txtName],
        index: index
      })
    },

    /**
     * 
     */
    initPagePosition() {
      return this.$refs.list.initPagePosition()
    }
  }
}



